.clr {
  @include clr; }

a {
  text-decoration: none;
  &:hover {
    text-decoration: underline; }
  &:active {
    text-decoration: underline; } }

.text-center {
  text-align: center; }

.text-right {
  text-align: right; }

.text-left {
  text-align: left; }

.mr40 {
  margin-right: 40px; }
.ml40 {
  margin-left: 40px; }
.mr20 {
  margin-right: 20px; }
.ml20 {
  margin-left: 20px; }
.pr40 {
  padding-right: 40px; }
.pl40 {
  padding-left: 40px; }

.section {
