.list {
  font-size: 16px;
  font-weight: 400;
  color: #000000;
  > li {
    margin-bottom: 10px;
    padding-left: 40px;
    padding-right: 40px; }
  &--unordered {
    li:before {
      content: 'â€¢';
      display: inline-block;
      vertical-align: baseline;
      color: #fc2db7;
      margin-right: 4px; } }
  &--ordered {
    counter-reset: list;
    > li {
      color: #000000;
      font-size: 22px;
      &:before {
        counter-increment: list;
        content: counter(list) '.';
        display: inline-block;
        vertical-align: baseline;
        color: #fc2db7;
        margin-right: 5px; }
      li {
        font-size: 16px; } } }
  &--simple {
    > li {
      font-size: 22px;
      li {
        font-size: 16px; } } }
  &__item {
    & + & {
      margin-top: 15px; }
    &--highlighted {
      background-color: #fff;
      color: #fc2db7;
      padding-top: 10px;
      padding-bottom: 15px;
      p {
        color: black;
        font-size: 16px; } } } }

.col-1,
.col-2 {
