.section.section--iframe {
  margin-top: 80px; }

.iframe {
  @include dib;
  &--fb {
    margin-top: 57px; }
  & + & {
    margin-left: 50px; } }

.form {
  padding-right: 20px;
  text-align: center;
  input {
    font-family: 'pfdin-regular'; }
  &__input {
    height: 40px;
    background-color: #ffffff;
    color: #444444;
    font-size: 18px;
    font-weight: 400;
    width: 597px;
    margin-left: 20px;
    padding-left: 20px;
    padding-right: 10px;
    &169 {
      width: 169px; }
    &362 {
      width: 362px; }
    &408 {
      width: 408px; } }

  &__text {
    margin-bottom: 17px;
    font-size: 18px; }
  textarea {
    color: #444444;
    font-size: 18px;
    font-weight: 400;
    height: 340px;
    background-color: #ffffff;
    padding-left: 20px;
    resize: none;
    width: 1213px;
    margin-left: 20px;
    border: none;
    margin-top: 20px;
    padding-bottom: 10px;
    padding-top: 10px;
    padding-right: 20px;
    font-family: 'pfdin-regular'; }
  &--block {
    margin-bottom: 70px;
    .form__input {
      display: block;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 20px; } } }

@each $cls in fb-small, vk-small {
  .icon-#{$cls} {
    @include s(icon-#{$cls});
    &:hover {
      @include s(icon-#{$cls}--hover); }
    &:active {
      @include s(icon-#{$cls}--release); } } }

.form--login {
  margin-left: 26px;
  input[type='submit'] {
    margin-top: 8px;
    padding: 7px 52px; } }

.form--reg {
  margin-top: 37px;
  input[type='submit'] {
    margin-top: 7px;
    padding: 7px 15px; } }

.share {
  span {
    display: inline-block;
    vertical-align: super;
    margin-right: 15px; }
  &__social {
    display: inline-block;
    li {
      display: inline-block;
      & + li {
        margin-left: 15px; } }
    .icon {
      display: inline-block; } } }

.index-page {
  .btn--more {
    padding: 7px 18px; } }

.train-page {
