.order-page {
  &.order-page--auth {
    .method {
      margin-top: 27px; } }
  &__sum {
    font-size: 22px;
    font-weight: 400;
    color: #000000;
    span {
      color: #444444;
      line-height: 30px; }
    p:last-of-type {
      margin-top: 10px; } }
  .form__label--radio {
    margin-bottom: 17px;
    display: block;
    color: #000000;
    font-size: 22px;
    font-weight: 400;
    input {
      display: none;
      &:checked + span {
        width: 17px;
        height: 17px;
        display: inline-block;
        background-color: #fff;
        border: 1px solid #9f9f9f;
        border-radius: 50%;
        position: relative;
        &:hover {
          background-color: #ddd; }
        &:after {
          content: '';
          display: inline-block;
          width: 5px;
          height: 5px;
          background-color: #444;
          border-radius: 50%;
          position: absolute;
          top: 5px;
          left: 5px; } } }
    span {
      width: 17px;
      height: 17px;
      display: inline-block;
      background-color: #fff;
      border: 1px solid #9f9f9f;
      border-radius: 50%;
      position: relative;
      margin-right: 9px;
      vertical-align: baseline;
      cursor: pointer;
      &:hover {
        background-color: #ddd; } } }

  .method {
    margin-top: 43px;
    &.payment-method {
      margin-top: 33px; } }
  .order-form {
    text-align: left;
    .order-form__submit {
      margin-top: -27px;
      input {
        margin-top: 0; } }
    &__next {
      margin-bottom: 40px;
      input {
        cursor: pointer; } }
    div + div {
      margin-top: 20px; }
    .form__input,
    textarea {
      @include placeholder {
        color: #444444;
        font-size: 18px;
        font-weight: 400; } }
    textarea {
      width: 1168px;
      height: 90px;
      background-color: #ffffff;
      margin-top: 0; } } }

.delivery-method {
